import React from 'react'
import ReactDOM from 'react-dom'
import { MemoryRouter as Router } from 'react-router-dom'
import Routes from './routes/Routes'
import uuid from './utils/uuid'
import EventManager from './EventManager'
import Providers from './Providers'
import Initializer from './Initializer'
import isSingleWebView from './utils/isSingleWebView'

const appRouterEvent = new EventManager()

function ChildApp (props) {
  const { appid, initLocation } = props
  const [inited, setInited] = React.useState(false)
  React.useEffect(() => {
    setInited(true)
  }, [])
  return (
    <Router>
      {inited
        ? (
          <Providers appid={appid}>
            <Routes />
          </Providers>
        )
        : <Initializer initLocation={initLocation} />}
    </Router>
  )
}

function reducer (state, action) {
  if (!isSingleWebView()) {
    return state
  }
  const { type, payload } = action
  // console.log(state, action)
  let prevAppid
  if (type === 'push') {
    if (state.length > 0) {
      prevAppid = state[state.length - 1].id
      // console.log(`.reactapp[data-reactappid="${state[state.length - 1].id}"]`)
      let currentEl = document.querySelector(`.reactapp[data-reactappid="${prevAppid}"]`)
      currentEl.style.display = 'none'
    }
    const nextAppid = payload.id
    let el = document.createElement('div')
    el.classList.add('reactapp')
    el.setAttribute('data-reactappid', payload.id)
    document.body.insertBefore(el, document.body.childNodes[0] || null)
    ReactDOM.render(<ChildApp appid={nextAppid} initLocation={payload.location} />, el)
    appRouterEvent.fireEvent('UTmeStateChange', { type, prevAppid, nextAppid })
    return state.concat(payload)
  }
  if (type === 'pop') {
    const nextState = state.slice()
    if (nextState.length === 1) {
      console.warn('Cannot pop anymore')
      return nextState
    }
    let route = nextState.pop()
    prevAppid = route.id
    let el = document.querySelector(`.reactapp[data-reactappid="${prevAppid}"]`)
    ReactDOM.unmountComponentAtNode(el)
    el.remove()
    const nextAppid = nextState[nextState.length - 1].id
    let nextEl = document.querySelector(`.reactapp[data-reactappid="${nextAppid}"]`)
    nextEl.style.display = 'block'
    appRouterEvent.fireEvent('UTmeStateChange', { type, prevAppid, nextAppid })
    return nextState
  }
  return state
}

function AppManager (props) {
  // console.log(props)

  const [stacks, dispatch] = React.useReducer(reducer, [], () => [])
  const [defaultAppId] = React.useState(uuid.v4())

  React.useEffect(() => {
    push(props.location)
  }, [])

  function pop () {
    dispatch({
      type: 'pop'
    })
  }

  function push (location) {
    if (process.env.REACT_APP_UNDO_MODE === 'disable') {
      if (stacks.length >= 1) {
        pop()
      }
    }
    dispatch({
      type: 'push',
      payload: {
        location,
        id: uuid.v4()
      }
    })
  }

  global.AppManager = {
    stacks,
    push,
    pop,
    appRouterEvent
  }

  if (isSingleWebView()) {
    return null
  }

  return (
    <Providers appid={defaultAppId}>
      <Routes />
    </Providers>
  )
}

export default AppManager
