import React, { useEffect, useState, useRef } from 'react'
import ReactDOM from 'react-dom'
import './PopupImportant.css'

/**
 * @return {HTMLElement}
 */
function getReactBucketPopup () {
  let el = document.querySelector(`#ReactBucketPopup`)
  if (!el) {
    el = document.createElement('div')
    el.id = 'ReactBucketPopup'
    document.body.appendChild(el)
  }
  return el
}

function Mask (props) {
  const { style, extraProps, onClick, getIcon, districtProps } = props
  return (
    <div
      onClick={onClick}
      {...extraProps}
      className='popupClassName'
      style={{
        ...style,
        // position: 'absolute',
        position: 'fixed',
        top: 0,
        right: 0,
        bottom: 0,
        left: 0,
        zIndex: 11
      }}
    >
      <div className={getIcon ? 'getIconClassName' : 'getIconClassName2'} style={{ right: districtProps === 'districtProps' ? 4 : 68 }}>
        {getIcon ? <img src={getIcon} alt='' /> : ''}
      </div>
      {props.children}
    </div>
    /* { {getIcon && <div className='getIconClassName' style={{ right: districtProps === 'districtProps' ? 4 : 68 }}><img src={getIcon} alt='' /></div>}{props.children}</div> } */
  )
}

export default function Popup (props) {
  const {
    initialVisible = false,
    maskCloseable = true,
    onVisibleChange = () => {},
    children,
    showMask = true,
    getIcon,
    districtProps,
    renderOverlay = () => <div />
  } = props

  const trigger = useRef(null)
  const overlay = useRef(null)
  const [visible, setVisible] = useState(initialVisible)
  const { iconVisible, setIconVisible } = props
  const [triggerRect, setTriggerRect] = useState(null)
  useEffect(() => {
    if (trigger.current) setTriggerRect(ReactDOM.findDOMNode(trigger.current).getBoundingClientRect())
  }, [trigger])

  function handleTriggerClick () {
    setIconVisible && setIconVisible(!iconVisible)
    setVisible(!visible)
    onVisibleChange(!visible)
    // if (document.getElementById('FiltersIcons')) {
    //   const FiltersIcons = document.getElementById('FiltersIcons')
    //   if (visible) {
    //     FiltersIcons.style.zIndex = 11
    //   } else {
    //     FiltersIcons.style.zIndex = 13
    //   }
    // }
  }

  function handleMaskClick () {
    // if (document.getElementById('FiltersIcons')) {
    //   const FiltersIcons = document.getElementById('FiltersIcons')
    //   if (maskCloseable) {
    //     setVisible(false)
    //     onVisibleChange(false)
    //     FiltersIcons.style.zIndex = 11
    //   }
    // } else {
    if (maskCloseable) {
      if (setIconVisible && typeof setIconVisible === 'function') {
        setIconVisible(!iconVisible)
      }
      setVisible(false)
      onVisibleChange(false)
    }
    // }
  }

  function closePopup () {
    setVisible(false)
    if (setIconVisible && typeof setIconVisible === 'function') {
      setIconVisible(!iconVisible)
    }
    onVisibleChange(false)
  }

  return (
    <>
      {React.cloneElement(children, {
        ref: trigger,
        onClick: props.disabled ? null : handleTriggerClick
      })}
      {ReactDOM.createPortal(
        <>
          <Mask style={{ display: (visible && showMask) ? 'block' : 'none' }} onClick={handleMaskClick} getIcon={getIcon} districtProps={districtProps} />
          <div style={{ display: visible ? 'block' : 'none' }}>
            {React.cloneElement(renderOverlay({
              triggerRect,
              closePopup,
              visible
            }), {
              ref: overlay
            })}
          </div>
        </>,
        getReactBucketPopup()
      )}
    </>
  )
}
