import React, { useContext, useState } from 'react';
import { MakeContext } from './MakeContext';
import useViewport, { getHeight, getWidth } from '../hooks/useViewport';
import invoke from '../utils/invoke';

/*
/  周俊update--2019.5.12
/  更改内容：编辑区域的位置计算公式，代码注释补充
*/
export const scaleModeEnums = {
  preview: 'preview',
  big: 'big',
  editing: 'editing',
  bigView: 'bigView',
  bigByKeyboard: 'bigByKeyboard',
  donePreview: 'donePreview',
  doneBig: 'doneBig',
  // narrowPreview: 'narrowPreview'
};

export const ItemContext = React.createContext({
  scaleMode: '',
  setScaleMode: '',
  dynamicDeviceWidth: 0, // 屏幕的宽，根据deviceWidth计算
  dynamicDeviceHeight: 0, // 屏幕的高，根据deviceHeight计算
  deviceWidth: 0, // 设备初始的宽
  deviceHeight: 0, // 设备初始的高
  imageSize: 0,
  imageStyle: {},
  imageSrc: '',
  doneImageSrc: '',
  areaStyle: {},
  doneStyle: {},
  keyboard: 0, // 编辑区域下方的高度，可能是键盘也可能是done页面的图片视图区域的下面部分，周俊add--2019.5.13
  width: 0,
  height: 0,
  scale: 1,
  translateY: 0,
  zoom: false,
  screenTopOffset: 83,
});

export function ItemProvider(props) {
  const { children } = props;
  const make = useContext(MakeContext);
  const [dynamicDeviceWidth, dynamicDeviceHeight] = useViewport();
  const [deviceWidth] = useState(getWidth());
  const [deviceHeight] = useState(getHeight());
  const [scaleMode, setScaleMode] = useState(scaleModeEnums.preview);
  const [zoom, setZoom] = useState(false);
  const [keyboard, setKeyboard] = useState(0);
  const lastItem = make.getBaseItem();
  const canvasLimit = 4096;
  const viewModulus = 8;
  const modulus = 2;
  const wkNearScale = 0.95;
  const realWidth = 2048;
  const realHeight = 2304;
  const canvasWidth = realWidth / modulus;
  const canvasHeight = realHeight / modulus;
  const IMAGE_WIDTH = realWidth / viewModulus;
  const IMAGE_HEIGHT = realHeight / viewModulus;
  const screenTopOffset = 83; // 手机上半部状态栏高度
  const screenBottomOffset = 94; // 手机下半部留白高度
  const screenTopEditorOffset = 46; // 编辑时手机上半部留白高度
  const screenBottomEditorOffset = 70; // 编辑时手机下半部留白高度

  const viewportHeight = deviceHeight - screenTopOffset - screenBottomOffset;
  const { design_layout = {} } = lastItem;
  const complete_layout = lastItem.complete_layout || design_layout;
  const { image } = design_layout;
  const { image: doneImage } = complete_layout;
  const multiply = design_layout.multiply === '1';
  const doneMultiply = complete_layout.multiply === '1';
  const center = design_layout.center.split(',');
  const imageWidth = IMAGE_WIDTH / design_layout.width;
  const imageHeight = imageWidth;
  const doneCenter = complete_layout.center.split(',');
  const doneImageWidth = IMAGE_WIDTH / complete_layout.width;
  const doneImageHeight = doneImageWidth;
  const GLOBAL_SCALE = viewportHeight / imageHeight;
  const GLOBAL_DONE_SCALE = viewportHeight / doneImageHeight;
  // let padding = Math.max(screenTopOffset, screenBottomOffset) / GLOBAL_SCALE;
  let padding = 0; // 上下白底
  let canvasSize = (imageWidth + padding + padding) * viewModulus / modulus;
  if (canvasSize > canvasLimit) {
    canvasSize = canvasLimit;
    padding = (canvasSize * modulus / viewModulus - imageWidth) / 2;
  }
  const left = (deviceWidth - imageWidth) * 0.5;
  const top = screenTopOffset + (viewportHeight - imageHeight) * 0.5;
  const doneLeft = (deviceWidth - doneImageWidth) * 0.5;
  const doneTop = screenTopOffset + (viewportHeight - doneImageHeight) * 0.5;
  const areaLeft = imageWidth * center[0] - IMAGE_WIDTH * 0.5;
  const areaTop = imageHeight * center[1] - IMAGE_HEIGHT * 0.5;
  const doneAreaLeft = doneImageWidth * doneCenter[0] - IMAGE_WIDTH * 0.5;
  const doneAreaTop = doneImageHeight * doneCenter[1] - IMAGE_HEIGHT * 0.5;
  let scale = GLOBAL_SCALE;
  let doneScale = GLOBAL_DONE_SCALE;
  let translateY = 0;
  let translateX = 0;
  let doneTranslateY = 0;
  let doneTranslateX = 0;
  if (keyboard > 0) {
    const viewportHeight2 = deviceHeight - screenTopEditorOffset - screenBottomEditorOffset - keyboard;
    scale = viewportHeight2 / IMAGE_HEIGHT;
    translateY = (screenTopEditorOffset - areaTop - top - (areaTop - imageHeight * 0.5) * (scale - 1)) / scale;
    translateX = imageWidth * 0.5 - areaLeft - IMAGE_WIDTH * 0.5;
  } else if (zoom) {
    scale = deviceWidth * wkNearScale / IMAGE_WIDTH;
    doneScale = scale;
    translateY = imageHeight * 0.5 - areaTop - IMAGE_HEIGHT * 0.5;
    translateX = imageWidth * 0.5 - areaLeft - IMAGE_WIDTH * 0.5;
    doneTranslateY = doneImageHeight * 0.5 - doneAreaTop - IMAGE_HEIGHT * 0.5;
    doneTranslateX = doneImageWidth * 0.5 - doneAreaLeft - IMAGE_WIDTH * 0.5;
  }

  const imageStyle = {
    width: imageWidth,
    height: imageHeight,
    left: left - padding,
    top: top - padding,
    padding,
  };

  const doneImageStyle = {
    width: doneImageWidth,
    height: doneImageHeight,
    left: doneLeft,
    top: doneTop,
  };

  const areaStyle = {
    position: 'absolute',
    width: IMAGE_WIDTH,
    height: IMAGE_HEIGHT,
    left: areaLeft + padding,
    top: areaTop + padding,
  };

  const doneStyle = {
    position: 'absolute',
    width: IMAGE_WIDTH,
    height: IMAGE_HEIGHT,
    left: doneAreaLeft,
    top: doneAreaTop,
  };

  const canvasArea = {
    width: canvasWidth,
    height: canvasHeight,
    left: (areaLeft + padding) * viewModulus / modulus,
    top: (areaTop + padding) * viewModulus / modulus,
  };

  return (
    <ItemContext.Provider
      value={{
        GLOBAL_SCALE,
        scale,
        doneScale,
        scaleMode,
        setScaleMode,
        dynamicDeviceWidth,
        dynamicDeviceHeight,
        deviceWidth,
        deviceHeight,
        imageStyle,
        doneImageStyle,
        imageSrc: image,
        doneImageSrc: doneImage,
        areaStyle,
        doneStyle,
        keyboard,
        setKeyboard,
        imageSize: imageWidth + padding + padding,
        imageHeight,
        translateY,
        translateX,
        doneTranslateY,
        doneTranslateX,
        zoom,
        setZoom,
        screenTopOffset,
        canvasSize,
        canvasArea,
        realWidth,
        realHeight,
        multiply: false,
        doneMultiply,
      }}
    >
      {children}
    </ItemContext.Provider>
  );
}
