import React, { Component } from 'react'
import './alternativePlugin.css'
import Navbar from '../../navbar'
import imgArry from './images'
import invoke from '../../../utils/invoke'
import Locale from '../../../components/Locale'
import IsAgainEdit from '../../../components/isAgainEdit'
import { MakeContext } from '../../MakeContext'
import { throttle, InitializationThrottle } from '../../../utils/throttle'
import LoadingModal from '../../Done/LoadingModal'
import { loadImage } from '../../../utils/loadImages'
import { getTopLayerIndex } from '../../../hoc/injectImages'
import AlertModal from '../../../components/AlertModal'
import imageTools from '../../../utils/imageTools'
/**
 * 获取限制
 */
var zeroArry = []

class Alternative extends Component {
  constructor (props) {
    super(props)
    this.state = {
      modelList: imgArry,
      limitArr: [],
      isAgainEdit: false,
      err: false,
      resetModalVisible: false,
      loadingVisible: false,
      alertModelVisible: false
    }
  }

  change = () => {
    this.setState({
      isAgainEdit: true
    })
  };
  isAgainFu = (e) => {
    this.setState({
      isAgainEdit: false
    })
  };

  render () {
    const { modelList, loadingVisible, alertModelVisible } = this.state
    return (
      <div className='Alternative'>
        <AlertModal visible={alertModelVisible} title='error.network.title' description='error.network.description' text='OK' onClick={() => {
          this.setState({
            alertModelVisible: false
          })
        }} />
        <AlertModal
          visible={this.state.resetModalVisible}
          title='WEBVIEW_ERROR_TITLE'
          description='WEBVIEW_ERROR_MESSAGE'
          text='OK'
          onClick={() => {
            invoke('navigateTo', {
              routeName: 'Make'
            })
          }}
        />
        <LoadingModal setVisible={() => this.setState({ loadingVisible: false })} visible={loadingVisible} />
        <Navbar currentPage='mixChoice' page='mixChoice' revoke={() => {
          this.change()
        }} />
        <div style={{
          padding: '0 7px 7px 7px',
          display: 'flex',
          flexWrap: 'wrap',
          justifyContent: 'center'
        }}>
          <ul className='ulList' id='ulList'>
            {
              modelList.map((v, k) => (
                <li onClick={() => v.type ? throttle(() => this.goDetails(v)) : () => this.prohibit(v)}
                  key={k}
                  style={{
                    width: (window.innerWidth - 35) / 2,
                    marginLeft: (k % 2) === 0 ? 5 : 5.5,
                    marginRight: (k % 2) === 0 ? 5.5 : 5,
                    marginBottom: 6
                  }}>
                  <div className='describeImg'>
                    <img src={v.src} alt='插件图标展示' style={{ pointerEvents: 'none', display: 'block' }} />
                  </div>
                  <div className='describeText'>
                    <Locale>{({ locale }) => (<img style={{ marginTop: k > 1 ? 24 : 14 }}
                      language={locale.current}
                      imgId={v.id}
                      src={locale.current === 'ja' ? v.ja_img : v.en_img} />)}</Locale>
                  </div>
                  {
                    v.type ? null : (
                      <div className='unavailable'>
                        <Locale>{({ locale }) => (<img style={{ width: '100%', height: '100%', pointerEvents: 'none' }}
                          src={locale.current === 'ja' ? v.moduleUnAvailableJa : v.moduleUnAvailableEn} />)}</Locale>
                      </div>
                    )
                  }

                </li>
              ))
            }
          </ul>
        </div>
        <IsAgainEdit status={this.state.isAgainEdit} isAgainFu={this.isAgainFu} />
      </div>
    )
  }

  componentWillMount () {
    this.setState({
      loadingVisible: true
    })
    InitializationThrottle()
  }

  componentDidMount () {
    // if(thimer.isCalled){
    //  thimer.isCalled=false
    // }
    try {
      const { modelList } = this.state
      var _this = this
      // const htm = document.getElementById('ulList')
      // htm.style.height = (window.innerHeight - 50) + 'px'
      // const htmLi = htm.getElementsByTagName('li')
      // htmLi[modelList.length - 1].style.marginBottom = 25 + 'px'
      var newArray = []
      var notUseArray = []
      var number = 0
      var acquiredNum = 0
      // 获取remix限制规则
      var getLayers = this.props.make.layers
      zeroArry = []
      if (getLayers) {
        if (getLayers[getLayers.length - 1] && getLayers[getLayers.length - 1].stampRule) {
          const stampRules = getLayers[getLayers.length - 1].stampRule
          if (stampRules.mixArr) {
            zeroArry.push(stampRules.mixArr)
          }
        } else {
          zeroArry = ['OK', 'OK', 'OK', 'OK', 'OK', 'OK']
        }
      }
      /**
       * 获取限制数据（）
       */
      if (this.state.limitArr.length === 0) {
        if (zeroArry.length !== 0) {
          _this.setState({
            limitArr: zeroArry
          })
          if (number === 0) {
            if (zeroArry) {
              window.clearInterval(this.getArry)
              number++
              if (zeroArry.length === 0) {
                _this.special(modelList)
              } else {
                var childOne = []
                var childTwo = []
                var childThree = []
                if (zeroArry[0]) {
                  childOne = zeroArry[0]
                } else {
                  childOne = ['', '', '', '', '', '']
                }
                if (zeroArry[1]) {
                  childTwo = zeroArry[1]
                } else {
                  childTwo = ['', '', '', '', '', '']
                }
                if (zeroArry[2]) {
                  childThree = zeroArry[2]
                } else {
                  childThree = ['', '', '', '', '', '']
                }
                var newType = []
                for (let t = 0; t < 6; t++) {
                  let one = childOne[t]
                  let two = childTwo[t]
                  let three = childThree[t]
                  if (one === 'NG' || two === 'NG' || three === 'NG') {
                    newType.push(false)
                    modelList[t].type = false
                  } else {
                    newType.push(true)
                    modelList[t].type = true
                  }
                }
                // invoke('log', 'newType = ' + JSON.stringify(newType))
                /**
                 * 列表位置动态调整
                 */
                for (let j = 0; j < modelList.length - 1; j++) {
                  let data = modelList[j]
                  if (data.type) {
                    newArray.push(data)
                  }
                }
                newArray.push(modelList[6])
                for (let s = 0; s < modelList.length - 1; s++) {
                  let data = modelList[s]
                  if (!data.type) {
                    notUseArray.push(data)
                  }
                }
                for (let l = 0; l < notUseArray.length; l++) {
                  let data = notUseArray[l]
                  if (!data.type) {
                    newArray.push(data)
                  }
                }
                this.setState({
                  modelList: newArray
                })
                _this.special(newArray)
              }
            }
          }
        } else {
          if (acquiredNum === 0) {
            acquiredNum++
            _this.special(modelList)
          }
        }
      }
      setTimeout(() => {
        this.setState({
          loadingVisible: false
        })
      }, 500)
    } catch (error) {
      this.setState({
        alertModelVisible: true
      })
      this.setState({
        err: true
      })
      setTimeout(() => {
        this.setState({
          loadingVisible: false
        })
      }, 800)
    }
  }

  special (e) {
    const htm = document.getElementById('ulList')
    const htmLi = htm.getElementsByTagName('li')
    /**
     * 绑定点击改变背景效果（除了不能点击的模块儿）
     */
    for (let z = 0; z < e.length; z++) {
      if (e[z].type) {
        htmLi[z].addEventListener('touchstart', function () {
          htmLi[z].style.backgroundColor = '#f3f3f3'
          const language = htmLi[z].childNodes[1].children[0].attributes[0].textContent
          const imgid = htmLi[z].childNodes[1].children[0].attributes[1].textContent
          const activeList = htmLi[z].childNodes[1].children[0]
          const imgActive = htmLi[z].childNodes[0].children[0]
          for (let i = 0; i < e.length; i++) {
            if (parseInt(imgid) === e[i].id) {
              imgActive.src = e[i].src_pressed
              if (language === 'ja') {
                activeList.src = e[i].ja_no_img
              }
              if (language === 'en') {
                activeList.src = e[i].en_no_img
              }
            }
          }
        })
        htmLi[z].addEventListener('touchend', function () {
          htmLi[z].style.backgroundColor = '#ffffff'
          const language = htmLi[z].childNodes[1].children[0].attributes[0].textContent
          const imgid = htmLi[z].childNodes[1].children[0].attributes[1].textContent
          const activeList = htmLi[z].childNodes[1].children[0]
          const imgActive = htmLi[z].childNodes[0].children[0]
          for (let i = 0; i < e.length; i++) {
            if (parseInt(imgid) === e[i].id) {
              imgActive.src = e[i].src
              if (language === 'ja') {
                activeList.src = e[i].ja_img
              }
              if (language === 'en') {
                activeList.src = e[i].en_img
              }
            }
          }
        })
      }
    }
  }

  changeStructure (layers) {
    // Deep Clone
    let objLayers = JSON.parse(JSON.stringify(layers))
    objLayers.forEach((item, index) => {
      if (item.stampRule) {
        // invoke('log', 'item.stampRule.stampFlag.flag_can_other_layer_over' + JSON.stringify(item.stampRule.stampFlag.flag_can_other_layer_over));
        if (item.stampRule.stampFlag.flag_can_other_layer_over === 'NG') {
          let str = objLayers.splice(index, 1)
          objLayers.push(str[0])
        }
      }
    })
    return objLayers
  }

  async goDetails (e) {
    try {
      let { layers, previousPage } = this.props.make
      const lastLayer = layers[layers.length - 1]
      lastLayer.mixType = e.id
      await invoke('setLayers', layers)
      if (e.name === 'NO REMIX') {
        // const newLayers = this.changeStructure(layers);
        // invoke('log', 'newLayers' + JSON.stringify(newLayers));
        this.setState({
          loadingVisible: true
        })
        const images = [
          layers.length > 1 ? imageTools.getImageData(`back-${layers.length - 1}`) : undefined,
          imageTools.getImageData(`front-${layers.length}`)
        ]
        const imagesData = await Promise.all(images)
        const isLayerTop = getTopLayerIndex(layers) === layers.length - 1
        let imageData = ''
        let topLayer = null
        if (layers.length > 1 && !isLayerTop) {
          const [back, front] = await Promise.all(imagesData.map(loadImage))
          const canvas = document.createElement('canvas')
          const ctx = canvas.getContext('2d')
          canvas.width = front.naturalWidth
          canvas.height = front.naturalHeight
          ctx.drawImage(back, 0, 0)
          ctx.drawImage(front, 0, 0)
          imageData = canvas.toDataURL()
        } else {
          const [back, front] = imagesData
          if (back) {
            imageData = back
            topLayer = front
          } else if (isLayerTop) {
            topLayer = front
            imageData = imageTools.emptyData
          } else {
            imageData = front
          }
        }
        await Promise.all([
          imageTools.storeImageData({ imageName: `back-${layers.length}`, imageData }),
          topLayer ? imageTools.storeImageData({ imageName: 'topLayer', imageData: topLayer }) : undefined
        ])
        this.setState({
          loadingVisible: false
        })
        await invoke('navigateTo', {
          to: '/make/Done',
          params: { previousPage }
        })
      } else if (e.name === 'MoMA EARLY MODERN STYLE') {
        invoke('navigateTo', {
          to: '/make/momaEarly',
          params: { previousPage }
        })
      } else if (e.name === 'MoMA COLLAGE') {
        invoke('navigateTo', {
          to: '/make/momaCollage',
          params: { previousPage }
        })
      } else if (e.name === 'AUTO DESIGNER') {
        invoke('navigateTo', {
          to: '/make/autoDesigner',
          params: { previousPage }
        })
      } else if (e.name === 'SPLASH') {
        invoke('navigateTo', {
          to: '/make/splash',
          params: { previousPage }
        })
      } else if (e.name === 'GLITCH') {
        invoke('navigateTo', {
          to: '/make/glitch',
          params: { previousPage }
        })
      } else if (e.name === 'MOSAIC') {
        invoke('navigateTo', {
          to: '/make/mosaic',
          params: { previousPage }
        })
      }
    } catch (e) {
      // this.setState({
      //   alertModelVisible:true
      // })
      this.setState({
        resetModalVisible: e.code === 'GET_IMAGE_DATA_ERROR',
        loadingVisible: false
      })
    }
  }

  prohibit (e) {
    // console.log(`不可操作模块儿`, e.name)
  }
}

export default props => (
  <MakeContext.Consumer>
    {(make) => (
      <Alternative {...props} make={make} />
    )}
  </MakeContext.Consumer>
)
