import React from 'react'
import { Redirect, Switch, Route } from 'react-router-dom'
import Make from './Make'
import mixChoice from './navbar/alternativePlugin/alternativePlugin'

const FullScreenItemMenu = React.lazy(() => import('./FullScreenItemMenu'))

export default function Routes (props) {
  return (
    <React.Suspense fallback={<div />}>
      <Switch>
        <Route path='/fullscreen-items' component={FullScreenItemMenu} />
        <Route path='/make' component={Make} />
        {/* {process.env.NODE_ENV === 'development' && <Route path='/test' component={mixChoice} />} */}
        <Route path='/mixChoice' component={mixChoice} />
        <Redirect path='/' to='/make' />
      </Switch>
    </React.Suspense>
  )
}
