import { useContext } from 'react'
import qs from 'querystring'
import { __RouterContext, matchPath } from 'react-router-dom'

export default function useRouter (options = {}) {
  let context = useContext(__RouterContext)
  let location = options.location || context.location
  let match = options.path
    ? matchPath(location.pathname, options)
    : context.match

  let routerState = {
    ...context,
    location,
    match
  }

  Object.defineProperty(routerState, 'query', {
    enumerable: false,
    get () {
      return qs.parse(location.search.substr(1))
    }
  })

  return routerState
}
