let eventList = []

export const $on = (eventName, cb) => {
  if (!eventList[eventName]) {
    eventList[eventName] = []
  }
  eventList[eventName].push(cb)
}

export const $emit = (eventName, params) => {
  if (eventList[eventName]) {
    let arr = eventList[eventName]
    arr.map((cb) => {
      cb(params)
    })
  }
}

let mapEventList = []
var test = 1
export const $mapOn = (eventName, cb, num) => {
  const options = {
    name: eventName,
    func: cb,
    num: num
  }
  if (mapEventList.length === 0) {
    mapEventList.push(options)
  }
}
export const $mapEmit = (eventName, params, num) => {
  if (mapEventList.length > 0) {
    for (let i = 0; i < mapEventList.length; i++) {
      if (eventName === mapEventList[i].name) {
        params.num = num
        mapEventList[i].func(params)
      }
    }
  }
}

let slideList = []
export const $slideOn = (eventName, cb, num) => {
  const options = {
    name: eventName,
    func: cb,
    num: num
  }
  if (slideList.length === 0) {
    slideList.push(options)
  }
}
export const $slideEmit = (eventName, params, num) => {
  if (slideList.length > 0) {
    for (let i = 0; i < slideList.length; i++) {
      if (eventName === slideList[i].name) {
        params.num = num
        slideList[i].func(params)
      }
    }
  }
}

export const $off = (eventName, cb) => {
  if (eventList[eventName]) {
    if (cb) {
      let index = eventList[eventName].indexOf(cb)
      eventList[eventName].splice(index, 1)
    } else {
      eventList[eventName].length = 0
    }
  }
}

export default {
  $on,
  $off,
  $emit
}
