import useRouter from './hooks/useRouter'

export default function Initializer (props) {
  const router = useRouter()

  const { initLocation } = props
  if (initLocation) {
    // console.log('initLocation', initLocation)
    // console.log('router', router)
    router.history.replace(`${initLocation.pathname}${initLocation.search}`)
    return null
  }

  const metaInitUrl = document.querySelector("meta[data-name='data-utme-init-url']")
  let initUrl = metaInitUrl ? metaInitUrl.content : null
  if (!initUrl || initUrl === 'undefined') return null

  const metaInitProps = document.querySelector("meta[data-name='data-utme-init-props']")
  const initProps = metaInitProps ? metaInitProps.content : null

  if (initProps) {
    router.history.replace(`${initUrl}?passProps=${initProps}`)
  } else {
    router.history.replace(`${initUrl}`)
  }

  return null
}
