import React, { useCallback, useEffect, useReducer, useState } from 'react'
import qs from 'querystring'
import useRouter from '../hooks/useRouter'
import code from '../utils/code'
import invoke from '../utils/invoke'
import brushColors from './paint/helpers/brushColors'

export const MakeContext = React.createContext({
  /**
   * 设置clothes color list的可见性
   */
  setColorListVisible: function () {
  },
  setColor: function () {
  },
  setSelectIcon: function () {
  },
  /**
   * clothes color list的可见性
   */
  isColorListVisible: false,
  isColor: '1',
  isSelectIcon: false,

  getBaseItem: () => {
  },

  /**
   * 导航条的高度
   */
  navbarHeight: 50,
  /**
   * 工具栏的高度
   */
  toolbarHeight: 50,
  pictureData: null,
  setPictureData () {
  },
  /**
   * clothList 基底照片数据
   */
  changeClothList () {
  },
  /**
   * subClothList 子菜单列表数据
   */
  subClothList: [],
  changeSubClothList () {
  },
  /**
   * isSubClothListVisible 子Popover是否可见
   */
  /**
   * 返回值
   */
  itemSelected: '',
  shareDesign: {},
  setShareDesign () {
  }
})

function ItemReducer (state, action) {
  if (action.type === 'ready') {
    return action
  }
  return state
}

export function MakeProvider (props) {
  const router = useRouter()
  const [{ type, layers }, dispatch] = useReducer(ItemReducer, { type: 'init', layers: [{}] })
  const [{ type: itemType, activeItem }, itemDispatch] = useReducer(ItemReducer, { type: 'init', activeItem: [] })
  let countryCode = ''
  let stampOnly = ''
  let newLayer = true
  let itemModalStatus = 'hidden'
  let isSavePhoto = true
  let previousPage = 'make'
  useEffect(() => {
    if (window.ReactNativeWebView) {
      if (type !== 'ready') {
        invoke('getLayers').then(items => dispatch({
          type: 'ready',
          layers: typeof items === 'string' ? JSON.parse(items) : items instanceof Array ? items : []
        }))
      }
      if (itemType !== 'ready') {
        invoke('getActiveItem').then(items => itemDispatch({
          type: 'ready',
          activeItem: typeof items === 'string' ? JSON.parse(items) : items instanceof Array ? items : []
        }))
      }
    } else {
      if (type !== 'ready') {
        dispatch({ type: 'ready', layers: [] })
      }
      if (itemType !== 'ready') {
        itemDispatch({
          type: 'ready',
          activeItem: [
            { init: true },
            {
              'id': '00',
              'display_new': '2014/02/24',
              'icon_image': 'https://s3.cn-north-1.amazonaws.com.cn//uq-production-bjn-newutme/uploads/plu_category/icon_image/1/6cf40af4-a658-424e-9b79-f044c419b9b3.png',
              'name': {
                'ja': 'ベーシックＴシャツ',
                'en': 'Basic T-shirt'
              }
            },

            {
              'item_code': '000000',
              'id': 'COL00',
              'color': '0xFFFFFF',
              'name': 'WHITE',
              'sold_out': true,
              'design_layout': {
                'image': 'https://s3.cn-north-1.amazonaws.com.cn//uq-production-bjn-newutme/uploads/plu_category/image/1/e9d5c2d0-af53-4a3c-a9b7-f8412b7e6e2a.png',
                'center': '0.5,0.491160220994475',
                'width': '0.49109883364027',
                'multiply': '1'
              },
              'complete_layout': null
            }
          ]
        })
      }
    }

    function handleMessage ({ data }) {
      try {
        data = JSON.parse(data)
      } catch (e) {
      }
      setActiveItem(data)
    }

    window.addEventListener('UTmeItemChange', handleMessage, false)
    return () => {
      window.removeEventListener('UTmeItemChange', handleMessage, false)
    }
  }, [])

  if (router.location.search) {
    const searches = qs.parse(router.location.search.substr(1))
    if (searches.passProps) {
      let passProps = code.decodeURI(searches.passProps)
      if (passProps.countryCode) {
        countryCode = passProps.countryCode
      }
      if (passProps.stampOnly) {
        stampOnly = passProps.stampOnly
      }
      if (passProps.itemModal) {
        itemModalStatus = passProps.itemModal
      }
      if (passProps.hasLayer) {
        newLayer = false
      }
      if (passProps.isSavePhoto === false) {
        isSavePhoto = passProps.isSavePhoto
      }
      if (passProps.previousPage === 'home') {
        previousPage = passProps.previousPage
      }
    }
  }

  const [navbarHeight, setNavbarHeight] = useState(50)
  const [toolbarHeight, setToolbarHeight] = useState(50)
  const [pictureData, setPictureData] = useState(null)
  const [subClothList, changeSubClothList] = useState([])
  const [isColorListVisible, setColorListVisible] = useState(false)
  const [color, setColor] = useState('red')
  const [isSelectIcon, setSelectIcon] = useState(false)
  const [shareDesign, setShareDesign] = useState({})
  // const [activeItem, setActiveItem] = useState(initialActiveItem);
  const [setStampVisible, changeSetStampVisible] = useState(false)
  const [textVisible, changeTextVisible] = useState(false)
  const [stampSet, changeStampSet] = useState(null)
  const [modalVisible, setModalVisible] = useState(false)
  const [contentsHolderIds, changeContentsHolderIds] = useState([])
  const [moreStampArr, changeMoreStampArr] = useState([])
  const [singleStampArr, changeSingleStampArr] = useState([])
  const [recentStampArr, changeRecentStampArr] = useState([])
  const [selectedStamp, changeSelectedStamp] = useState(null)
  const [preStamp, changePreStamp] = useState(null)
  const [uniqloStamp, changeUniqloStamp] = useState([])
  const [lastUpdateTime, changeLastUpdateTime] = useState(null)
  const [itemSelected, setItemSelected] = useState('')
  const [itemSelectedColor, setItemSelectedColor] = useState('')
  const [layerStep, setLayerStep] = useState(0)
  const [stampTitleName, changeStampTitleName] = useState('')
  const [revokeToMake, setRevokeToMake] = useState(false)

  // layers为图层数据存储的数组
  // const [layers, setLayers] = useState(newLayers)
  function setLayers (layers) {
    invoke('setLayers', layers)
  }

  // function setPictureData(data) {
  //   pictureDispatch({ type: 'ready', pictureData: data });
  // }

  function setActiveItem (data) {
    itemDispatch({ type: 'ready', activeItem: data })
  }

  // 选择item是否显示弹框
  const [itemModal, setItemModal] = useState(itemModalStatus)

  // 设置取过来的stamp_set
  const [stampList, setStampList] = useState({})

  const [lastUpdated, setLastUpdated] = useState('')

  // 设置刷子的color
  const [brushColor, setBrushColor] = useState(brushColors[0])

  const getBaseItem = useCallback(function getBaseItem () {
    const len = activeItem.length
    if (len === 0) {
      return {
        design_layout: {
          center: '0.5,0.4911602209944751',
          width: '0.4910988336402701'
        },
        complete_layout: {
          center: '0.5,0.4911602209944751',
          width: '0.4910988336402701'
        }
      }
    }
    let baseItem = activeItem[len - 1]
    if (!baseItem.icon_image) {
      let index = len - 2
      while (index >= 0) {
        if (activeItem[index].icon_image) {
          baseItem.icon_image = activeItem[index].icon_image
          break
        }
        index--
      }
    }
    return baseItem
  }, [activeItem])

  if (type === 'ready' && itemType === 'ready') {
    return (
      <MakeContext.Provider
        value={{
          setActiveItem,
          activeItem,
          getBaseItem,
          stampOnly,
          countryCode,
          isSavePhoto,
          previousPage,
          pictureData,
          setPictureData,
          navbarHeight,
          setNavbarHeight,
          toolbarHeight,
          setToolbarHeight,
          isColorListVisible,
          setColorListVisible,
          setColor,
          subClothList,
          changeSubClothList,
          color,
          isSelectIcon,
          setSelectIcon,
          shareDesign,
          setShareDesign,
          setStampVisible,
          changeSetStampVisible,
          textVisible,
          changeTextVisible,
          stampSet,
          changeStampSet,
          modalVisible,
          setModalVisible,
          contentsHolderIds,
          changeContentsHolderIds,
          moreStampArr,
          changeMoreStampArr,
          singleStampArr,
          changeSingleStampArr,
          selectedStamp,
          changeSelectedStamp,
          preStamp,
          changePreStamp,
          recentStampArr,
          changeRecentStampArr,
          uniqloStamp,
          changeUniqloStamp,
          lastUpdateTime,
          changeLastUpdateTime,
          itemSelected,
          setItemSelected,
          itemSelectedColor,
          setItemSelectedColor,
          stampTitleName,
          changeStampTitleName,
          layerStep,
          setLayerStep,
          layers,
          setLayers,
          itemModal,
          setItemModal,
          stampList,
          setStampList,
          brushColor,
          setBrushColor,
          lastUpdated,
          setLastUpdated,
          newLayer,
          revokeToMake,
          setRevokeToMake,
          appid: props.appid
        }}
      >
        <div
          id='root>div'
          style={{
            position: 'fixed',
            top: 0,
            right: 0,
            left: 0,
            bottom: 0,
            overflow: 'auto'
          }}
        />
        <div style={{
          position: 'absolute',
          top: 0,
          bottom: 0,
          left: 0,
          right: 0
          // zIndex:2
        }}>
          {props.children}
        </div>
      </MakeContext.Provider>
    )
  } else {
    return null
  }
}
