import React, { useContext } from 'react'
import { Modal } from 'antd-mobile'
import Locale from './Locale'
import { LocaleContext } from '../contexts/LocaleContext'
import './alertModal.css'

export default function AlertModal (props) {
  const languageType = useContext(LocaleContext)

  const { visible, title, description, utid = '', footer = [{ text: 'OK', onPress: props.onClick, style: { fontWeight: 'bold', color: '#007aff', height: '43px', lineHeight: '43px', fontSize: '17px', background: 'none' } }] } = props
  return (
    <Modal
      transparent
      maskClosable={false}
      visible={visible}
      animationType='fade'
      className='alertModel'
      wrapClassName='WrapAlertModelClass'
      footer={footer.map(item => {
        return (
          item
        )
      })}
    >
      <div style={{ padding: '0 15px', paddingBottom: '21.5px', background: 'none' }}>
        {title ? <div>
          <span
            style={{
              fontFamily: languageType.current === 'ja' ? 'NotoSansCJKjp-Bold' : 'initial',
              fontWeight: languageType.current === 'ja' ? 'initial' : 'bold',
              fontSize: languageType.current === 'ja' ? '17px' : '17px',
              textAlign: 'center',
              color: '#1b1b1b',
              lineHeight: languageType.current === 'ja' ? '24px' : '22px'
            }}
          ><Locale code={title} />{utid}</span></div> : ''
        }
        {description ? <div
          style={{
            fontFamily: languageType.current === 'ja' ? 'NotoSansCJKjp-Regular' : 'initial',
            // fontWeight: languageType.current === 'ja' ? 'initial' : 'normal',
            fontSize: languageType.current === 'ja' ? '13px' : '13px',
            textAlign: 'center',
            color: '#1b1b1b',
            whiteSpace: 'pre-wrap'
          }}>
          <Locale code={description} />
        </div> : ''
        }
      </div>
    </Modal>
  )
}
