import invoke from './invoke'
import isSingleWebView from './isSingleWebView'

let sequence = 10001

const cachedImageData = {}

const emptyData = 'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAYAAAAfFcSJAAAAC0lEQVQYV2NgAAIAAAUAAarVyFEAAAAASUVORK5CYII='

/**
 *
 * @param {string} imageName
 * @returns {Promise<string>}
 */
async function invokeGetImage (imageName) {
  try {
    const image = await invoke('getImage', imageName)
    return image
  } catch (e) {
    e.code = 'GET_IMAGE_DATA_ERROR'
    throw e
  }
}

export default {
  cache: true, // 是否使用本地缓存
  emptyData,
  storeImageData (data) {
    if (isSingleWebView() && this.cache) {
      const { imageName, imageData } = data
      // invoke('log','imageName:'+imageName+' imageData:'+imageData)
      cachedImageData[imageName] = imageData
    } else {
      return invoke('storeImage', data)
    }
  },
  getImageData (imageName) {
    if (isSingleWebView() && this.cache) {
      const imageData = new Promise((resolve, reject) => {
        // Test code for reset make
        // const error = new Error('getImageDataError')
        // error.code = 'GET_IMAGE_DATA_ERROR'
        // return reject(error)

        let data = cachedImageData[imageName]
        if (!data) {
          setTimeout(() => {
            data = cachedImageData[imageName]
            if (!data) {
              const getImage = invokeGetImage(imageName)
              getImage.then(imageData => {
                cachedImageData[imageName] = imageData
                resolve(imageData)
              })
                .catch((e) => {
                  reject(e)
                })
            } else {
              resolve(data)
            }
          }, 1000)
        } else {
          resolve(data)
        }
      })
      return imageData
    }
    return invokeGetImage(imageName)
  },
  storeImage (rawData = {}) {
    sequence += 1

    const currentDate = Date.now()

    const success = 'utmreactimagesucessfn' + currentDate + sequence
    const fail = 'utmreactimagefailfn' + currentDate + sequence

    return new Promise((resolve, reject) => {
      window[success] = () => {
        resolve && resolve()
      }

      window[fail] = (err) => {
        reject && reject(err)
      }
      const param = {
        functionName: 'storeImage',
        success,
        fail,
        ...rawData
      }
      window.ReactNativeWebView.postMessage(JSON.stringify(param))
    })
  },
  getImage (imageName) {
    sequence += 1

    const currentDate = Date.now()

    const success = 'utmreactimagesucessfn' + currentDate + sequence
    const fail = 'utmreactimagefailfn' + currentDate + sequence

    return new Promise((resolve, reject) => {
      window[success] = (res) => {
        resolve && resolve(res.result)
      }

      window[fail] = (err) => {
        reject && reject(err)
      }

      const param = {
        functionName: 'getImage',
        imageName,
        success,
        fail
      }
      window.ReactNativeWebView.postMessage(JSON.stringify(param))
    })
  },

  /**
   *
   * @param {HTMLCanvasElement} picIn
   * @param {Number} width
   * @param {Number} height
   * @param {Number} realWidth
   * @param {Number} realHeight
   * @returns {HTMLCanvasElement}
   */
  scaleImage (picIn, width, height, realWidth, realHeight) {
    const canvas = document.createElement('canvas')
    const context = canvas.getContext('2d')
    canvas.width = realWidth
    canvas.height = realHeight
    context.drawImage(picIn, 0, 4, width - 4, height - 4, 0, 0, realWidth, realHeight)
    return canvas
  },
  /**
   *
   * @param {Number} width
   * @param {Number} height
   * @param {HTMLImageElement[]} images
   * @returns {HTMLCanvasElement}
   */
  mergeImage (width, height, images, alwaysReturnCanvas = false) {
    if (images.length > 1 || alwaysReturnCanvas) {
      const canvas = document.createElement('canvas')
      const context = canvas.getContext('2d')
      canvas.width = width
      canvas.height = height
      images.forEach(image => context.drawImage(image, -1, 3))
      return canvas
    }
    return images[0]
  },
  multiplyImage (back, front, style, canvasSize, canvas, isTopLayer = false) {
    const { width, height } = style
    const ctx = canvas.getContext('2d')
    ctx.clearRect(0, 0, width, height)
    ctx.globalCompositeOperation = 'source-over'
    ctx.drawImage(back, -style.left, -style.top, canvasSize, canvasSize)
    if (isTopLayer) {
      ctx.globalCompositeOperation = 'destination-in'
      ctx.drawImage(front, 0, 0, width, height)
    }
    ctx.globalCompositeOperation = 'multiply'
    ctx.drawImage(front, 0, 0, width, height)
    return canvas
  }
}
