import React, { useContext, useState, useEffect } from 'react'
import ItemMenu from '../components/ItemMenu'
import itemPicker from './itemPicker.css'
import Popup from './Popup'
import { MakeContext } from '../routes/MakeContext'
import { ItemContext } from '../contexts/ItemContext'
import { $emit } from '../components/observer'
import invoke from '../utils/invoke'
import AlertModal from '../components/AlertModal'
export default function ItemPicker (props) {
  const make = useContext(MakeContext)
  const { zoom } = useContext(ItemContext)
  const [isPopupVisible, setPopupVisible] = useState(false)
  const [itemNum, setItemNum] = useState(0)
  const [alertModelVisible, setAlertModelVisible] = useState(false)
  async function handleSelect (item, { closePopup }) {
    if (!item.colors) {
      make.setItemSelectedColor(item.id)
      // make.setItemSelected('')
    } else {
    }
    $emit('handle', 'switched')
    try {
      await closePopup()
    } catch (error) {
      closePopup()
    }
  }

  function getIcon () {
    const baseItem = make.getBaseItem()
    return baseItem.icon_image || require('../assets/251556542319_.pic.jpg')
  }
  async function getItems () {
    let result
    try {
      result = await invoke('getItems')
    } catch (error) {
      setAlertModelVisible(true)
      // TODO 这里异常可能性几乎为0，因为items是打开app时加载的。
      // 加载items失败进不了app
      // 所以暂不处理
    }
    setItemNum(result.length)
  }
  useEffect(() => { getItems() }, [])
  return (
    <Popup
      disabled={props.disabled}
      districtProps={props.districtProps}
      onVisibleChange={setPopupVisible}
      getIcon={getIcon()}
      renderOverlay={(overlayProps) => {
        const { triggerRect, closePopup, visible } = overlayProps
        setPopupVisible(visible)
        if (!triggerRect) return <div />
        return (
          <div style={{
            zIndex: 12,
            position: 'fixed',
            // backgroundColor: '#FFF',
            width: window.innerWidth,
            left: 0,
            // top: triggerRect.top + triggerRect.height + 10,
            top: 58
          }}>
            <AlertModal visible={alertModelVisible} title='error.network.title' description='error.network.description' text='OK' onClick={() => { setAlertModelVisible(false) }} />
            {/* 三角形 */}
            <div style={{
              position: 'absolute',
              top: 0,
              right: props.districtProps === 'districtProps' ? 9 : 73
              // left: triggerRect.left + (triggerRect.width - 30) / 2
            }}>
              {/* <svg width='30' height='20' viewBox='0 0 30 20'>
                <path
                  fill='#fff'
                  d='M15 4 L30 20 L0 024 z'
                />
              </svg> */}
              <img
                src={require('../assets/bg-triangle.png')}
                alt=''
                style={{ width: 30, height: 20 }}
              />
            </div>
            <div style={{ top: 20, position: 'absolute', right: 0, left: 0, bottom: 0, backgroundColor: '#fff' }}>
              <ItemMenu isPopupVisible={isPopupVisible} onSelect={(item) => handleSelect(item, { closePopup })} />
            </div>
          </div>
        )
      }}
    >
      {
        itemNum === 1 ? <div />
          : <div
          className="itemImageIcon" style={{
            borderRadius: 20,
            width: props.isBorder || props.page === 'mixChoice' ? 'auto' : 38,
            top: 2,
            right: props.right ? props.right : 68,
            position: 'absolute',
            objectFit: 'containt',
            height: props.isBorder || props.page === 'mixChoice' ? 'auto' : 38,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            border:zoom&&(make.activeItem[2].id!=='COL00')||props.page === 'mixChoice' || props.isBorder?'1px solid transparent':'1px solid #ddd',
            backgroundColor: 'rgba(255,255,255,0.3)',
          }}>
            <img src={getIcon()} className='itemImg' alt='icon' style={{ width: 24, height: 24, marginTop: props.remixPropsother === 'remixProps' ? 8 : 0 }} />
          </div>
      }
    </Popup>
  )
}
