import { Base64 } from 'js-base64'

export function encode (object) {
  return Base64.encode(JSON.stringify(object))
}

export function decode (string) {
  return JSON.parse(Base64.decode(string))
}

export function encodeURI (object) {
  return encodeURIComponent(JSON.stringify(object))
}

export function decodeURI (string) {
  return JSON.parse(decodeURIComponent(string))
}

export default {
  encodeURI,
  decodeURI,
  encode,
  decode
}
