
// import { useContext } from 'react'
// import { MakeContext } from '../routes/MakeContext'
export default function dataRevoke (page, layers) {
  // console.log('判断当前从哪个页面返回', page)
  // const make = useContext(MakeContext)
  // const {
  //   layers,
  //   setLayers
  // } = make
  let layersArr = layers
  if (page === 'picture' || page === 'text' || page === 'paint' || page === 'stamp' || page === 'resetLayers') {
    layersArr.pop()
  } else if (page === 'setStamp') {
    delete layersArr[layersArr.length - 1].stampRule
  } else if (page === 'mixSelect') {
    delete layersArr[layersArr.length - 1].mixType
  } else if (page === 'mix') {
    delete layersArr[layersArr.length - 1].mix
  } else if (page === 'done') {
    delete layersArr[layersArr.length - 1].mixData
  }
  // invoke('log', 'layersArr' + JSON.stringify(layersArr))
  return layersArr
}
