import React from 'react'
import { MakeProvider } from './contexts/MakeContext'
import { LocaleProvider } from './contexts/LocaleContext'
import { BridgeSubscribeProvider } from './contexts/BridgeSubscribeContext'

export default function Providers (props) {
  const { appid } = props

  return (
    <LocaleProvider>
      <MakeProvider appid={appid}>
        <BridgeSubscribeProvider>
          {props.children}
        </BridgeSubscribeProvider>
      </MakeProvider>
    </LocaleProvider>
  )
}
