import React, { useContext } from 'react'
import { LocaleContext } from '../contexts/LocaleContext'

export default function Locale (props) {
  const { is, code, children, params = [], ...extraProps } = props
  const locale = useContext(LocaleContext)
  const isChildrenFunc = typeof children === 'function'
  let computedChildren = null
  let raw = locale.pkg[code]
  if (!raw) {
    raw = code
  } else {
    for (const param of params) {
      raw = raw.replace('%s', param)
    }
  }
  if (isChildrenFunc) {
    computedChildren = children({ code, locale, raw })
  } else {
    computedChildren = raw
  }
  if (!is) return computedChildren
  return React.createElement(is, extraProps, computedChildren)
}
