import React, { useState } from 'react'
import qs from 'querystring'
import code from '../utils/code'
import useRouter from '../hooks/useRouter'
import ja from '../locales/ja'
import en from '../locales/en'

export const LocaleContext = React.createContext({
  current: ''
})

export function LocaleProvider (props) {
  const router = useRouter()
  let [current] = useState(getInitialLocale())

  function getInitialLocale () {
    if (router.location.search) {
      const searches = qs.parse(router.location.search.substr(1))
      if (searches.passProps) {
        let passProps = code.decodeURI(searches.passProps)
        if (passProps.lang) {
          return passProps.lang
        }
      }
    }
    return 'ja'
  }

  let pkg = {}
  if (current === 'ja') pkg = ja
  if (current === 'en') pkg = en
  return (
    <LocaleContext.Provider
      value={{
        current,
        pkg
      }}
    >
      {props.children}
    </LocaleContext.Provider>
  )
}
