let timer
var isCalled = false

/**
 * @param functionTobeCalled 被包装的方法
 */

export const throttle = (functionTobeCalled, interval = 4000, isSuo) => {
  if (isSuo) {
    isCalled = false
  }
  // console.log(123123123123,isCalled)
  if (!isCalled) {
    isCalled = true
    clearTimeout(timer)
    timer = setTimeout(() => {
      isCalled = false
    }, interval)
    if (functionTobeCalled) {
      return functionTobeCalled()
    }
  }
}
export const InitializationThrottle = () => {
  isCalled = false
}

