import React, { useContext, useEffect, useState } from 'react'
import invoke from '../utils/invoke'
import { MakeContext } from '../routes/MakeContext'
import AlertModal from '../components/AlertModal'
import imageTools from '../utils/imageTools'

export function getTopLayerIndex (layers) {
  /* eslint-disable */
  return layers.findIndex(({ flag_can_other_layer_over }) => flag_can_other_layer_over === 'NG')
  /* eslint-enable */
}

export default function injectImages (...items) {
  const front = items.indexOf('front') > -1
  const back = items.indexOf('back') > -1
  const photo = items.indexOf('photo') > -1
  return (Clazz) => (props) => {
    const { layers } = useContext(MakeContext)
    const level = layers.length
    const topIndex = getTopLayerIndex(layers)
    const [[mixData, data, pictureData, topLayer], setImages] = useState([])
    const [ready, setReady] = useState(false)
    const isLayerTop = topIndex === level - 1
    const [error, setError] = React.useState(null)

    useEffect(() => {
      const images = new Array(4)
      if (back || level > 1) {
        images[0] = imageTools.getImageData(`back-${level - (back ? 0 : 1)}`)
      }
      if (front) {
        images[1] = imageTools.getImageData(`front-${level}`)
      }
      if (photo) {
        images[2] = invoke('getPictureData')
      }
      if (topIndex > -1 && (!isLayerTop || back)) {
        images[3] = imageTools.getImageData('topLayer')
      }

      Promise.all(images).then(images => {
        setImages(images)
        setReady(true)
      }).catch((e) => {
        setError(e)
      })
    }, [])
    if (error) {
      return (
        <AlertModal
          visible
          title='WEBVIEW_ERROR_TITLE'
          description='WEBVIEW_ERROR_MESSAGE'
          text='OK'
          onClick={() => {
            invoke('navigateTo', {
              routeName: 'Make'
            })
          }}
        />
      )
    }
    if (ready || !global.ReactNativeWebView) {
      return <Clazz {...props} mixData={mixData} data={data} pictureData={pictureData} topLayer={topLayer} isLayerTop={isLayerTop} />
    }
    return null
  }
}
