import code from '../utils/code'
import mockItems from '../mocks/items.json'
import { Loading } from '../components/loading'
import imageTools from './imageTools'
import instruction from '../constants/instruction'
import isSingleWebView from './isSingleWebView'

let currentId = 0

function getId () {
  currentId++
  return `reactfn${currentId}`
}

const mockFns = {
  goBack: (e) => {
    if (e === 'setStamp') {
      // console.log(e)
    }
    window.history.back()
  },
  getItems: () => {
    return mockItems
  },
  request: () => {
  },
  log: (data) => {
    console.log(data)
  },
  storeImage: () => {
  },
  getImage: () => '',
  getBackAndFront: () => {
    return []
  },
  getStorageItem: ({ key }) => {
    if (key in instruction) {
      return true
    }
  },
  getGlobal: () => true
}

const isIOS = () => {
  let u = navigator.userAgent
  return !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/) // ios终端
}

export default function invoke (fn, rawData = {}) {
  // if (fn === 'navigateTo') {
  //   const {params} = rawData
  //   if (params) {
  //     const {layers} = params
  //     if (layers) {
  //       invoke('setLayers', JSON.stringify(layers))
  //       delete params.layers
  //     }
  //   }
  // }
  // ios WKWebview 桥接内存泄漏，使用原生桥接

  // if (global.ReactNativeWebView && isIOS()) {
  //   if (fn === 'storeImage') {
  //     return imageTools.storeImage(rawData)
  //   } else if (fn === 'getImage') {
  //     return imageTools.getImage(rawData)
  //   }
  // }

  return new Promise(async (resolve, reject) => {
    let data = {
      _data: rawData,
      fn,
      fnId: getId()
    }

    let responsed = false
    window[data.fnId] = function (msg) {
      if (responsed) return
      responsed = true
      try {
        const result = code.decode(msg)
        if (result.error) {
          // TODO 所有的地方都处理了异常的话，把下面这行删掉
          // return resolve(result)

          let error = new Error(result.message)
          error.name = result.error
          reject(error)
        } else {
          resolve(result)
        }
      } catch (e) {
        // TODO 可能异常的情况是code.decode失败了，目前为止没放生过
        // 如果发生了，这里可能需要强化处理逻辑
        reject(e)
      }
      window[data.fnId] = null
      delete window[data.fnId]
    }

    /**
     * 自动添加参数
     */
    if (fn === 'navigateTo') {
      if (!data._data.params) {
        data._data.params = {}
      }
      let autoAddParamNames = ['designId', 'lang']
      let matchPassProps = global.location.href.match(/passProps=.*/)
      if (matchPassProps) {
        try {
          let passPropsString = matchPassProps[0].substr('passProps='.length)
          let passProps = code.decodeURI(passPropsString)
          for (let param of autoAddParamNames) {
            if (typeof passProps[param] !== 'undefined' && typeof data._data.params[param] === 'undefined') {
              data._data.params[param] = passProps[param]
            }
          }
        } catch (e) {
        }
      }

      const { routeName, to, params = {} } = data._data

      if (!routeName) {
        if (isSingleWebView()) {
          global.AppManager.push({
            hash: '',
            pathname: to,
            search: `?passProps=${code.encodeURI(params)}`
          })
          return
        }
      }
    }

    if (fn === 'goBack') {
      if (isSingleWebView()) {
        if (global.AppManager.stacks.length > 1) {
          global.AppManager.pop()
          return
        }
      }
    }

    if (global.ReactNativeWebView) {
      if (fn === 'log' && process.env.NODE_ENV === 'production') return resolve()
      global.ReactNativeWebView.postMessage(code.encode(data))
      if (fn === 'navigateTo') {
        Loading({ type: false })
      }
    } else {
      if (mockFns[fn]) {
        try {
          resolve(await mockFns[fn](data._data))
        } catch (e) {
          reject(e)
        }
      } else {
        // if (!process.env.REACT_APP_IGNORE_NOT_IN_UTME) global.alert('Not in UTme! App.')
      }
    }
  })
}

function main () {
  if (process.env.NODE_ENV === 'development') {
    // invoke('log', 'if you see this, it means log method successed')
  }
}

main()
