import React, { useEffect, useContext, useState } from 'react'
import invoke from '../utils/invoke'
import asyncStates from '../utils/asyncStates'
import Locale from '../components/Locale'
import { MakeContext } from '../routes/MakeContext'
import { LocaleContext } from '../contexts/LocaleContext'
// import { itemSelected } from '../contexts/MakeContext'
import checkpng from '../assets/check.png'
import './itemMenu.css'
import AlertModal from '../components/AlertModal'

export default function ItemMenu (props) {
  const make = useContext(MakeContext)
  const [itemState, setItemState] = useState()
  const [items, setItems] = useState([])
  const [colorsCode, setColorsCode] = useState([])
  const [storageItemSelect, setStorageItemSelect] = useState([])
  const [colorsId, setColorsId] = useState([])
  const [colorArr, setColorArr] = useState([])
  const [selected, setSelected] = useState([{init: true}])
  const [menuHeight, setMenuHeight] = useState(585)
  let isheight
  if (window.screen.height > 647 || window.screen.height === 647) {
    isheight = 56
  } else {
    isheight = 42
  }
  const [_height, set_height] = useState(isheight)
  const [_menuHeight, set_menuHeight] = useState(585)
  const {
    layers
  } = make
  let district = useContext(LocaleContext)
  async function getItems () {
    try {
      setItemState(asyncStates.pending)
      let result
      try {
        result = await invoke('getItems')
      } catch (err) {
        // TODO 这里异常可能性几乎为0，因为items是打开app时加载的。
        // 加载items失败进不了app
        // 所以暂不处理
      }
      // invoke('log', 'kkkkkk' + JSON.stringify(JSON.parse(storageItemSelect)))
      // setStorageItemSelect(storageItemSelect)
      setItemState(asyncStates.fulfilled)
      setItems(result)
      // invoke('log', 'resultresult' + JSON.stringify(result))
      // alert('getItems')
      getStorageItem(result)
    } catch (e) {
      // global.alert(e)
      setItemState(asyncStates.rejected)
    }
  }

  async function getStorageItem (result) {
    let itemSelectObj = {
      newItemId: [],
      newColorId: []
    }
    let newlayers
    if (JSON.stringify(window.location.href).indexOf('fullscreen-items') !== -1) {
      newlayers = [{}]
    } else {
      newlayers = layers
    }
    newlayers.forEach((item) => {
      if (item.stampRule) {
        itemSelectObj.newItemId = [...itemSelectObj.newItemId, ...item.stampRule.itemId]
        itemSelectObj.newColorId = [...itemSelectObj.newColorId, ...item.stampRule.colorId]
      }
    })

    let storageItemSelect = itemSelectObj.newItemId
    let colorId = itemSelectObj.newColorId
    let colora = colorId
    let colorb = []
    colora.forEach(element => {
      element.forEach((item) => {
        colorb.push(item)
      })
    })
    let colors = Array.from(new Set(colorb))
    setColorArr(colorId)
    setColorsId(colors)
    let itemArr = Array.from(new Set(storageItemSelect))
    setStorageItemSelect(itemArr)
    let colorsCode2 = []
    result.forEach((item) => {
      if (item.colors) {
        if (itemArr.indexOf(item.id) > -1) {
          let index = itemArr.indexOf(item.id)
          item.colors.forEach((i) => {
            if (colorId[index].indexOf(i.id) > -1) {
              colorsCode2.push(i.item_code)
            }
          })
        }
      }
    })
    setColorsCode(colorsCode2)
    if (colorsCode2.indexOf(make.activeItem[2].item_code) > -1) {
      let activeItem = [{init: true}, {
        selected: true,
        id: result[0].id,
        name: result[0].name,
        icon_image: result[0].icon_image
      }, {
        selected: true,
        id: result[0].colors[0].id,
        design_layout: result[0].colors[0].design_layout,
        complete_layout: result[0].colors[0].complete_layout,
        name: result[0].colors[0].name,
        color: result[0].colors[0].color,
        item_code: result[0].colors[0].item_code,
        icon_image: result[0].colors[0].icon_image
      }]
      make.setActiveItem(activeItem)
      await invoke('setActiveItem', {activeItem})
      await invoke('isSelect', {name: make.activeItem[2].name, type: 'conflict'})
    }
  }

  useEffect(() => {
    let cacheBGColor = document.body.style.backgroundColor
    document.body.style.backgroundColor ='transparent'
    getItems()

    function handleClick (e) {
      if (e.target.className === 'temMenuCon') {
        invoke('closeItemMenu')
      }
    }

    if (JSON.stringify(window.location.href).indexOf('fullscreen-items') !== -1) {
      // invoke('log','innerHeight'+window.innerHeight)
      if (window.innerHeight < 560) {
        setMenuHeight(440)
      } else {
        setMenuHeight(504)
      }
    } else if (JSON.stringify(window.location.href).indexOf('fullscreen-items') === -1) {
      invoke('log', 'innerHeight2' + window.innerHeight)
      if (window.innerHeight < 647) {
        setMenuHeight(440)
      } else {
        setMenuHeight(504)
      }
    }
    document.getElementsByClassName('itemMenuWrap')[0].parentNode.className = 'temMenuCon'
    document.getElementsByClassName('itemMenuWrap')[0].parentNode.addEventListener('click', handleClick, true)
    // if(props.closeMenu){
    //   document.getElementById('root>div').style.zIndex = 1
    //   document.getElementById('root>div').addEventListener('click',function(){
    //     props.closeMenu()
    //     document.getElementById('root>div').style.zIndex = 'initial'
    //   })
    // }
    return () => {
      document.body.style.backgroundColor = cacheBGColor
      // document.getElementsByTagName('html')[0].style.backgroundColor ='#fff'
      document.getElementsByClassName('itemMenuWrap')[0].parentNode.removeEventListener('click', handleClick, true)
    }
  }, [window.innerHeight, window.location.href])

  useEffect(() => {
    const AlDom = document.getElementsByClassName('Alternative')[0]
    if (props.isPopupVisible === false) {
      pop()
      if (AlDom) {
        AlDom.parentNode.style.overflow = 'auto'
      }
    } else {
      if (AlDom) {
        AlDom.parentNode.style.overflow = 'hidden'
      }
    }
  }, [props.isPopupVisible])

  async function pushActiveItem (activeItem) {
    try {
      make.setActiveItem(activeItem)
      await invoke('setActiveItem', {activeItem})

      if (make.itemModal === 'show' || (JSON.stringify(window.location.href).indexOf('fullscreen-items') !== -1)) {
        await invoke('itemHomeModal', activeItem)
        invoke('log', 'activeItem222' + JSON.stringify(activeItem))
      }
    } catch (e) {
      // global.alert(e)
    }
  }

  function handleItemSelect (item) {
    const nextSelected = selected.slice()
    // invoke('log', 'JSON.stringify(item)' + JSON.stringify(item))
    if (item.colors) {
      make.setItemSelected(item.id)
    }
    nextSelected.push({
      selected: true,
      id: item.id,
      design_layout: item.design_layout,
      complete_layout: item.complete_layout,
      name: item.name,
      color: item.color,
      item_code: item.item_code,
      icon_image: (() => {
        if (selected.length === 1) return item.icon_image
        return selected[selected.length - 1].icon_image
      })()
    })
    // if (item.hasOwnProperty('isSelect')) {
    //   props.onSelect(item)
    //   pushActiveItem(nextSelected)
    //   return
    // }
    // invoke('log', 'selected' + JSON.stringify(item))
    if (item.colors) {
      if (item.colors.length > 1) {
        setSelected(nextSelected)
      } else {
        nextSelected.push({
          selected: true,
          id: item.colors[0].id,
          design_layout: item.colors[0].design_layout,
          complete_layout: item.colors[0].complete_layout,
          name: item.colors[0].name,
          color: item.colors[0].color,
          item_code: item.colors[0].item_code,
          icon_image: (() => {
            if (selected.length === 1) return item.colors[0].icon_image
            return selected[selected.length - 1].icon_image
          })()
        })
        if (props.onSelect) props.onSelect(item)
        pushActiveItem(nextSelected)
      }
    } else {
      if (props.onSelect) props.onSelect(item)
      pushActiveItem(nextSelected)
      // pop()
    }
    // global.alert(item.id)
  }

  function pop () {
    if (selected.length === 1) return
    const nextSelected = selected.slice()
    nextSelected.pop()
    setSelected(nextSelected)
  }

  const isAndroid = navigator.userAgent.indexOf('Android') > -1 || navigator.userAgent.indexOf('Adr') > -1 //android终端
  let bottom = 10
  const currentList = getCurrentListByPath(items, selected)

  function setItemHeight (params) {
    const totalHeight = (window.screen.height - window.innerHeight > 98) ? window.screen.height - 78 : (window.screen.height - window.innerHeight === 98) ? (window.screen.height) : (window.innerHeight)
    const cl = currentList.length
    if (window.screen.height - window.innerHeight > 100) {
      invoke('handleWebViewLoad')
    }
    let _panelHeight = totalHeight - bottom - (20 + 42 + 16)
    if (isAndroid) {
      if (_panelHeight > 56 * (cl + 1)) {
        set_height(56)
        set_menuHeight(56 * cl)
      } else {
        if (((_panelHeight) / (cl + 1) > 42) && ((_panelHeight) / (cl + 1)) < 56) {
          set_menuHeight(_panelHeight - (_panelHeight) / (cl + 1))
          set_height((_panelHeight) / (cl + 1))
        } else {
          set_menuHeight(42 * cl)
          set_height(42)
        }
      }
    } else {
      if (_panelHeight >= (56 * (cl + 1))) {
        set_height(56)
        set_menuHeight(56 * cl)
      } else {
        if ((_panelHeight / (cl + 1) > 42) && ((_panelHeight) / (cl + 1)) < 56) {
          set_menuHeight(_panelHeight - (_panelHeight) / (cl + 1))
          set_height((_panelHeight) / (cl + 1))
        } else {
          set_menuHeight(_panelHeight - 42)
          set_height(42)
        }
      }
    }
  }

  useEffect(() => {
    if (currentList.length) {
      setItemHeight()
    }
  }, [items, selected])

  return (
    <div className='itemMenuWrap' style={{backgroundColor: 'transparent'}}>
      {/* {(() => {
        if (itemState === asyncStates.pending) return 'loading...'
        if (itemState === asyncStates.none) return 'loading...'
        if (itemState === asyncStates.fulfilled) return 'fulflled'
        if (itemState === asyncStates.rejected) return 'rejected'
      })()} */}
      {currentList.length ? <div className='itemMenu_titleContent' style={{backgroundColor: '#fff', height: _height}}>
        {selected.length > 1 && (
          <div onClick={pop} style={{marginLeft: 5, width: 24, height: 24}}>
            <img src={require('../assets/bc.png')} style={{width: 24, height: 24}} alt=''/>
          </div>
        )}
        {/* <div className={district.current === 'ja' ? ' menutitlejan' : 'menutitleng'}> */}
        {selected.length > 1
          ? <div className={district.current === 'en' ? ' menutitleng' : 'menutitlejanother'}><Locale
            code='make.item.menu.title.color'/></div>
          : <div className={district.current === 'en' ? ' menutitleng' : 'menutitlejan'}><Locale
            code='make.item.menu.title'/></div>}
        {/* </div> */}
      </div> : ''}

      {selected.map((selectedOne, index) => {
        return (
          <Page
            key={selectedOne.id}
            index={index}
            _height={_height}
            _menuHeight={_menuHeight}
            pagePath={selected}
            items={items}
            onItemSelect={handleItemSelect}
            isColorMode={index > 1}
            make={make}
            storageItemSelect={storageItemSelect}
            colorsId={colorsId}
            colorArr={colorArr}
            // setColorsCodefu={(param) => setColorsCode(param)}
            colorsCode={colorsCode}
            menuHeight={menuHeight}
          />
        )
      })}
    </div>
  )
}

function Page (props) {
  const {pagePath, index, onItemSelect, items, make, storageItemSelect, colorsId, colorArr, colorsCode, menuHeight} = props
  const locale = useContext(LocaleContext)
  const visible = pagePath.length === index + 1
  const currentList = getCurrentListByPath(items, pagePath)
  const isColorMode = index > 0
  const [alertModelVisible, setAlertModelVisible] = useState(false)

  // invoke('log', 'currentList' + JSON.stringify(currentList))
  async function isSelectFu (name) {
    try {
      await invoke('isSelect', {name: name, type: 'prohibit'})
    } catch (e) {
    }

  }

  // let colorsCode2 = []

  // currentList.forEach((item) => {
  //   if (item.colors) {
  //     if (storageItemSelect.indexOf(item.id) > -1) {
  //       let index = storageItemSelect.indexOf(item.id)
  //       item.colors.forEach((i) => {
  //         if (colorArr[index].indexOf(i.id) > -1) {
  //           colorsCode2.push(i.item_code)
  //         }
  //       })
  //     }
  //   }
  // })
  // invoke('log', 'colorsCode' + JSON.stringify(colorsCode2))
  // if (colorsCode2.length > 0) {
  //   setColorsCodefu(colorsCode2)
  // }
  function handleClick (item) {
    let colorAllselected = false
    if (item.colors && storageItemSelect.indexOf(item.id) > -1) {
      let index = storageItemSelect.indexOf(item.id)
      if (colorArr.length > 0) {
        if (item.colors.length === colorArr[index].length) {
          colorAllselected = true
        }
      }
    }

    if (colorAllselected || colorsCode.indexOf(item.item_code) > -1) {
      // invoke('log', 'itemitemitem' + JSON.stringify(item))
      isSelectFu(item.name)
      item.isSelect = false
      // onItemSelect(item)
    } else {
      onItemSelect(item)
    }
  }

  //
  return (
    // <div style={{ display: visible ? 'block' : 'none', maxHeight: window.innerHeight < 650 ? 504 : 'initial', overflow: 'auto' }}>
    <div className="item____menu"
       style={{
         display: visible ? 'block' : 'none',
         height: window.screen.height - window.innerHeight >= 98 ? 'unset' : props._menuHeight,
         overflow: 'hidden',
         overflowY: props._height === 42 ? 'auto' : 'hidden',
         position: props._height === 42 ? 'absolute' : 'unset',
         left: 0,
         right: 0,
         top: props._height,
         bottom: window.screen.height - window.innerHeight >= 98 ? 10 : 'unset'
         }}>
      <AlertModal visible={alertModelVisible} title='error.network.title' description='error.network.description' text='OK' onClick={() => { setAlertModelVisible(false) }}/>
      {currentList.map(item => {
        let colorAllselected = false
        if (item.colors) {
          if (storageItemSelect.indexOf(item.id) > -1) {
            let index = storageItemSelect.indexOf(item.id)
            if (item.colors.length === colorArr[index].length) {
              colorAllselected = true
            }
          }
        }

        return (
          <div
            key={item.id}
            onClick={() => handleClick(item)}
            style={{
              position: 'relative',
              height: props._height,
              //   paddingLeft:visible?12:19,
              paddingRight: 16,
              boxShadow: colorsCode.indexOf(item.item_code) > -1 || colorAllselected ? 'inset 0 -1px 0 0 #ababab' : 'inset 0 -1px 0 0 #dadada',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'space-between',
              backgroundColor: colorsCode.indexOf(item.item_code) > -1 || colorAllselected ? '#dadada' : 'white',
              // opacity: colorsCode.indexOf(item.item_code) > -1 || colorAllselected ? 0.5 : 1
              color: colorsCode.indexOf(item.item_code) > -1 || colorAllselected ? '#ababab' : '#1b1b1b'
            }}>

            <div style={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'flex-start',
              width: '100%'
            }}>
              <div>
                {isColorMode
                  ? <div style={{
                    width: 56,
                    height: 18,
                    textAlign: 'center',
                    disply: 'flex',
                    justifyContent: 'center',
                    position: 'relative'
                  }}>
                    <div style={{
                      position: 'absolute',
                      left: 19,
                      width: 18,
                      height: 18,
                      border: item.color === '0xFFFFFF' ? '1px solid #eee' : 'none',
                      borderRadius: 20,
                      backgroundColor: `#${item.color.substr(2)}`
                    }}/>
                  </div>
                  : <div style={{width: 48, height: 56, marginRight: 24, position: 'relative'}}>
                    <img src={item.icon_image} alt=''
                         style={{width: 36, height: 36, position: 'absolute', left: 12, top: 10, filter: colorsCode.indexOf(item.item_code) > -1 || colorAllselected ? 'brightness(2.8)' : 'none'}}/>
                    {/* {console.log('make.itemSelected', make.itemSelected, 'and item', item)} */}

                    {make.activeItem[1].id === item.id ? <img src={checkpng} alt='' style={{
                      display: 'inlineBlock',
                      width: 23,
                      position: 'absolute',
                      left: 30,
                      top: 27
                    }}/> : ''}
                  </div>
                }
              </div>
              {/* <div className='itemMenu_word'>{isColorMode ? item.name : item.name[locale.current]}</div> */}
              {!isColorMode ? <div className={locale.current === 'en' ? 'itemEng' : 'itemJan'}
                                   style={{marginRight: item.colors instanceof Array && item.colors.length > 1 ? 0 : 63,
                                     color: colorsCode.indexOf(item.item_code) > -1 || colorAllselected ? '#ababab' : '#1b1b1b'
                                   }}>{item.name[locale.current]}</div> :
                <div className='itemMenucenter'>
                  <div className='itemMenu_word'>{item.name}</div>
                </div>}
              <div>
                {colorsCode.indexOf(item.item_code) > -1 || colorAllselected
                  ? <img src={require('../assets/prohibit.svg')} alt='' style={{
                        position: 'absolute',
                        width: 24,
                        height: 24,
                        right: 16,
                        top: 16
                      }} /> 
                  : item.colors instanceof Array && item.colors.length > 1
                    ? <div style={{
                      display: 'flex',
                      alignItems: 'center',
                      marginLeft: 15
                    }}>
                      <img src={require('../assets/typeColorBtnRed.svg')} alt='' style={{width: 24, height: 24}}/>
                      <img src={require('../assets/arrow@2x.png')} alt='' style={{width: 24, height: 24}}/>
                    </div>
                    : <div>
                      {make.activeItem[2].item_code === item.item_code ? <img src={checkpng} alt='' style={{
                        position: 'absolute',
                        width: 24,
                        height: 24,
                        right: 16,
                        top: 16
                      }}/> : ''}
                    </div>
                }
              </div>
            </div>
          </div>
        )
      })}
    </div>
  )
}

function getCurrentListByPath (items, pagePath) {
  let clonedPagePath = pagePath.slice()
  let currentList = []
  while (true) {
    if (clonedPagePath.length === 0) break
    const currentPage = clonedPagePath.shift()
    if (currentPage.init) {
      currentList = items.slice()
    } else {
      let item = currentList.find(item => item.id === currentPage.id)
      currentList = item.colors
    }
  }
  return currentList
}
