import React, { useContext, useState, useEffect } from 'react'
import backNormal from '../../assets/back/back_normal.svg'
import backPressed from '../../assets/back/back_pressed.svg'
import returnNormal from '../../assets/return/return_normal.svg'
import returnPressed from '../../assets/return/return_pressed.svg'
// import backPressed from '../../assets/Back_Pressed_Circle.svg'
// import backPressed from '../../assets/
import invoke from '../../utils/invoke'
import { Route, Switch } from 'react-router-dom'
import ItemPicker from '../../components/ItemPicker'
import dataRevoke from '../../utils/revokeLayer'
import { MakeContext } from '../MakeContext'
import { LocaleContext } from '../../contexts/LocaleContext'
import './index.css'
import { Loading } from '../../components/loading'
import Locale from '../../components/Locale'
import './navbar.css'
import { throttle, InitializationThrottle } from '../../utils/throttle'
import AlertModal from '../../components/AlertModal'
import { ItemContext } from '../../contexts/ItemContext'
export default function NavigationBar (props) {
  const make = useContext(MakeContext)
  const { zoom } = useContext(ItemContext)
  const {
    layers,
    previousPage
  } = make

  useEffect(() => {
    InitializationThrottle()
  }, [])
  async function handleClickGoBack () {
    try {
      if (props.back) {
        props.back()
      }
      if (props.page) {
        let newLayers = dataRevoke(props.page, layers)
        // setLayers(newLayers)
        await invoke('setLayers', newLayers)
      }
      throttle('', '', true)
      if (previousPage === 'home') {
        if (JSON.stringify(layers[0]) === '{}' || JSON.stringify(layers) === '[]') {
          invoke('navigateTo', {
            routeName: 'Home'
          })
          return
        }
      }
      if (props.pageMark) {
        invoke('goBack', props.pageMark)
      } else {
        invoke('goBack')
      }
    } catch (e) {
      setAlertModelVisible(true)
    }
  }

  // function revoke () {
  //   if (props.revoke()) {
  //     props.revoke()
  //   }
  //   // else{
  //
  //   // }
  // }

  async function handleClickCart () {
    Loading({ type: true })
    try {
      const urls = await invoke('getAnnouncementUrls')
      invoke('navigateTo', {
        routeName: 'URLScreen',
        params: {
          closeBtn: true,
          uri: urls.cart_url
        }
      })
    } catch (error) {
      setAlertModelVisible(true)
    }
  }
  const { current } = useContext(LocaleContext)
  const [backTouch, setBackTouch] = useState(false)
  const [restTouch, setRestTouch] = useState(false)
  const [alertModelVisible, setAlertModelVisible] = useState(false)
  const pageWhite = ['stamp', 'mixChoice', 'stampShowcase'].indexOf(props.page) > -1
  return (
    <div className='remix' style={{
      display: props.page === 'stampShowcase' ? 'block' : 'flex',
      height: '44px',
      justContent: 'center',
      alignItems: 'center',
      lineHeight: props.page === 'stampShowcase' ? '44px' : 'normal',
      top: 0,
      // paddingTop: props.page === 'stamp' || props.page === 'stampShowcase' ? 0 : 2,
      right: 0,
      position: props.currentPage === 'home' ? 'static' : 'fixed',
      left: 0,
      zIndex: props.currentPage === 'home' ? 0 : 10,
      paddingLeft: 11,
      paddingRight: props.page === 'stampShowcase' ? 74 : 14,
      bottom: 0,
      backgroundColor: props.currentPage ? '#fff' : '',
      transform: 'translateZ(0)'
    }}>
      <AlertModal visible={alertModelVisible} title='error.network.title' description='error.network.description' text='OK' onClick={() => { setAlertModelVisible(false) }} />
      <div className='remix-left' style={{ display: 'flex', justifyContent: 'flex-start', alignItems: 'center', height: '100%' }}>
        <div onClick={props.disabled ? null : handleClickGoBack} className='arrowLeft' style={{ border: zoom && (make.activeItem[2].id !== 'COL00') || pageWhite || props.isBorder ? '1px solid transparent' : '1px solid rgb(221, 221, 221)', height: pageWhite || props.isBorder ? 24 : 38, width: pageWhite || props.isBorder ? 24 : 38 }}>
          <img onTouchStart={e => setBackTouch(true)}
            onTouchEnd={e => setBackTouch(false)} src={backTouch ? backPressed : backNormal} />
        </div>
        <div onClick={props.disabled ? null : props.revoke} className='arrowRight' style={{ border: zoom && (make.activeItem[2].id !== 'COL00') || pageWhite || props.isBorder ? '1px solid transparent' : '1px solid rgb(221, 221, 221)', height: pageWhite || props.isBorder ? 24 : 38, width: pageWhite || props.isBorder ? 24 : 38 }}>
          <img onTouchStart={e => setRestTouch(true)}
            onTouchEnd={e => setRestTouch(false)} src={restTouch ? returnPressed : returnNormal} />
        </div>
      </div>
      <Switch>
        <Route exact path='/make'>
          <ItemPicker disabled={props.disabled} isBorder={props.isBorder} right={12} districtProps={'districtProps'} remixPropsother={'remixProps'} />
          <div className={current === 'en' ? 'navBarJanTitle' : 'navBar'}>
            <Locale code='make.home.title' />
            {/* 1.Create graphic image */}
          </div>
        </Route>
        <Route exact path='/make/stamp'>
          {current === 'en' ? <div className='navBarEng'>Select a sticker set</div> : <div className='navBarJan'>スタンプセットをえらんでください</div>}
        </Route>
        <Route exact path='/make/stamp/stampShowcase'>
          <div style={{ display: 'initial', height: 32, paddingTop: 12, textAlign: 'center', flex: 1 }}>
            <div style={{
              fontSize: 20,
              fontWeight: 'bold',
              marginRight: 14,
              marginLeft: 77,
              flex: 1,
              fontFamily: ['Roboto', 'Helvetica Neue', 'NotoSansCJKjp'],
              color: '#1b1b1b'

            }}><div style={{ width: '100%',
                overflow: 'hidden',
                textOverflow: 'ellipsis',
                whiteSpace: 'nowrap' }}>{make.stampTitleName}</div></div>
          </div>
        </Route>
        <Route exact path='/mixChoice'>
          <ItemPicker disabled={props.disabled} right={12} page={props.page} districtProps={'districtProps'} remixPropsother={'remixProps'} />
          <div className={current === 'en' ? 'stampWindowcaseNav' : 'stampWindowcaseJan'}>
            <div className={current === 'en' ? 'stampWindowcaseNavText' : 'stampWindowcaseNavTextJan'}
              style={{
                // width: current === 'ja' ? 226 : 194,
                fontSize: current === 'ja' ? 17 : 20,
                height: current === 'ja' ? 25 : 30
              }}><Locale code='remix.title' /></div>
          </div>
        </Route>
        <Route exact path='/make/Done'>
          <ItemPicker disabled={props.disabled} />
          <div onClick={() => throttle(handleClickCart)} style={{ position: 'absolute', top: 2, right: 12 }}>
            <svg xmlns='http://www.w3.org/2000/svg' width='40' height='40' viewBox='0 0 40 40'>
              <g fill='none' fill-rule='evenodd' transform='matrix(-1 0 0 1 40 0)'>
                <circle cx='20' cy='20' r='19.5' fill='#FFF' stroke='#DADADA' />
                <path fill='#D8D8D8' fill-opacity='.01' d='M31 9H9v22h22z' />
                <path fill='#1B1B1B'
                  d='M30.083 10.833h-2.421l-1.277 3.588H9.917l3.36 9.857h12.325l4.481-13.445zM23.921 28.76a1.68 1.68 0 1 0 0-3.36 1.68 1.68 0 0 0 0 3.36zm-8.963 0a1.68 1.68 0 1 0 0-3.36 1.68 1.68 0 0 0 0 3.36z' />
              </g>
            </svg>
          </div>
        </Route>
        <Route>
          <ItemPicker disabled={props.disabled} />
        </Route>
      </Switch>
      {props.children}
    </div>
  )
}
