import React, { useContext } from 'react'
import { Switch, Route } from 'react-router-dom'
import { MakeContext } from './MakeContext'
import { ItemProvider } from '../contexts/ItemContext'

const ErrorPage = React.lazy(() => import('./error/ErrorPage'))
const Home = React.lazy(() => import('./home'))
const Text = React.lazy(() => import('./text'))
const Picture = React.lazy(() => import('./picture'))
const Stamp = React.lazy(() => import('./stamp'))
const Paint = React.lazy(() => import('./paint'))
const Welcome = React.lazy(() => import('./home/Welcome'))
const SetStamp = React.lazy(() => import('./stamp/SetStamp'))
const Done = React.lazy(() => import('./Done'))
const StampShowcase = React.lazy(() => import('./stamp/StampShowcase'))
const MomaEarly = React.lazy(() => import('./Mix/MomaEarly'))
const MomaCollage = React.lazy(() => import('./Mix/MomaCollage'))
const AutoDesigner = React.lazy(() => import('./Mix/AutoDesigner'))
const Splash = React.lazy(() => import('./Mix/Splash'))
const Glitch = React.lazy(() => import('./Mix/Glitch'))
const Mosaic = React.lazy(() => import('./Mix/Mosaic'))

export default function Routes (props) {
  const make = useContext(MakeContext)

  let HomeComponent = null
  if (make.stampOnly) {
    HomeComponent = Welcome
  } else {
    HomeComponent = Home
  }
  return (
    <Switch>
      <Route path='/make' exact component={HomeComponent} />
      <Route path='/make/stamp' exact component={Stamp} />
      <Route path='/make/stamp/stampShowcase' exact component={StampShowcase} />
      <Route path='/make/errorPage' exact component={ErrorPage} />
      <Route>
        <ItemProvider>
          <Switch>
            <Route path='/make/Done' exact component={Done} />
            <Route path='/make/text' exact component={Text} />
            <Route path='/make/picture' exact component={Picture} />
            <Route path='/make/stamp/setStamp' exact component={SetStamp} />
            <Route path='/make/paint' exact component={Paint} />
            <Route path='/make/momaEarly' exact component={MomaEarly} />
            <Route path='/make/momaCollage' exact component={MomaCollage} />
            <Route path='/make/autoDesigner' exact component={AutoDesigner} />
            <Route path='/make/splash' exact component={Splash} />
            <Route path='/make/glitch' exact component={Glitch} />
            <Route path='/make/mosaic' exact component={Mosaic} />
          </Switch>
        </ItemProvider>
      </Route>
    </Switch>
  )
}
