import React, { useReducer, useEffect } from 'react'
import code from '../utils/code'

export const BridgeSubscribeContext = React.createContext({

})

export function BridgeSubscribeProvider (props) {
  const [values, dispatch] = useReducer(reducer, {}, () => ({}))

  useEffect(() => {
    window.__updateBridgeContext = function __updateBridgeContext (rawPayload) {
      let payload = code.decode(rawPayload)
      if (payload.toString() !== '[object Object]') {
        return
      }
      dispatch({
        type: 'init',
        payload
      })
    }
  }, [])

  function reducer (state, action) {
    switch (action.type) {
      case 'init':
        return action.payload || { }
      default:
        return state || {}
    }
  }

  return (
    <BridgeSubscribeContext.Provider value={{ values }}>
      {props.children}
    </BridgeSubscribeContext.Provider>
  )
}
